@use "../mixins";

.map {
  width: 100%;
  height: 362px;

  .map__image {
    border: none;
    display: block;
    width: 100%;
    height: 100%;
  }
}

@include mixins.tablet-w () {
  .map {
    width: 100%;
    min-height: 400px;
  }

}

@include mixins.desktop-w () {
  .map {
    width: 100%;
    height: 400px;
  }
}
